.song-container{
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    img{
        border-radius: 50%;
        padding: .2rem;
        border: 1px solid rgb(143, 33, 134);
        width:20%; box-shadow: .2px .2px 50px 1px rgba(143, 33, 134, 0.502);
    }
    h2{
        padding: 3rem 1rem 1rem;
    }
    h3{
        font-size: 1rem;
    }
}
@media screen and (max-width:768px){
    .song-container{
        img{
            width:60%;
        }
    }
}