.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control{
    width: 50%;
    display: flex;
    input{
        width: 100%;
     
    }
   p{
    padding: 1rem;
   }
}
.player-control{
    width: 30rem;
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  
}
@media screen and (max-width:768px){
   .time-control{
       width: 90%;
   }
   .player-control{
       width: 60%;
   }
}