.library{
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    background-color: rgb(41, 41, 41);
    height: 100%;
    overflow: scroll;
    border-right:3px solid rgb(143, 33, 134) ;
    box-shadow: 1px 1px 50px 10px rgb(143, 33, 134);
    
    transition: all .4s ease;
    transform: translateX(-100%);
    opacity: 0;
    .lib-title{
       padding: 2rem;
    }
   
}

.library-song{
 
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;
    cursor: pointer;
    transition: background .5 ease;
    img{
        width: 40%;
    }
    .song-info{
      padding-left: 1rem;
      h3{
          font-size: 1rem;
      } 
      h4{
          font-size: .7rem;
      }
    }
    &:hover{
        background-color:rgb(143, 33, 134) ;
    }
}
.selected{
    background-color: rgb(211, 179, 196);
}
.active{
    transform: translateX(0%);
    opacity: 1;
    }

    @media screen and (max-width:768px){
        .library{
            width: 100%;
        }
    
    }