nav{
    padding: 5vh 0;
    display:flex;
    align-items: center;
    justify-content: space-around;
    button{
        font-size: 1rem;
        padding:.3rem .7rem;
        position: relative;
        cursor: pointer;
        z-index: 1000;
        background-color: rgb(50, 50, 50);
        border:1px solid rgb(143, 33, 134);
        box-shadow: .2px .2px 10px 1px rgb(143, 33, 134);
        color: #fff;
        transition: all .3s ease;
        &:hover{
            background-color: rgb(143, 33, 134);
        }
        &:focus{
            outline: none;
            border: none;
         }
    }
    
}
